@font-face {
  font-family: "Roustel";
  src: url(../fonts/Roustel-Regular.ttf) format("ttf");
  font-display: swap;
}
@font-face {
  font-family: "Roustel";
  src: url(../fonts/Roustel-Regular.woff) format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roustel";
  src: url(../fonts/Roustel-Regular.woff2) format("woff2");
  font-display: swap;
}
:root {
  --color-main: #faf7f2;
  --color-primary: #f1ede7;
  --color-secondary: #f1ede7;
  --color-dark: #1e1f1e;
  --color-light: #fff;
  --text-button: #1e1f1e;
  --color-button-trans: #1e1f1e;
  --vd-color-pink: #e9088a;
  --color-gradient: linear-gradient(180deg, var(--color-primary) 0%, transparent 100%);
  --border-radius: 20px;
}

body {
  font-family: "Open Sans", sans-serif;
  color: var(--color-dark);
  hyphens: manual;
}

.main-app {
  position: relative;
  background-color: var(--color-main);
  overflow: hidden;
}
.main-app .background-body-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.main-app .background-body-wrapper .background-body-content img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 767px) {
  .main-app .background-body-wrapper .background-body-content img {
    object-position: right;
    object-fit: contain;
  }
}
.main-app .elements-wrapper-absolute {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.main-app .elements-wrapper-absolute .background-element {
  height: 100%;
}
.main-app .elements-wrapper-absolute .background-element img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.main-app .elements-absolute {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.main-app .elements-absolute .background-element {
  position: relative;
  width: 100%;
}
.main-app .elements-absolute .background-element img {
  width: 100%;
  height: auto;
}

.logo-viding {
  height: 2.4rem;
}
@media (max-width: 767px) {
  .logo-viding {
    height: 2.25rem;
  }
}
.logo-viding path {
  fill: var(--vd-color-pink);
}

.color__button__trans {
  background-color: transparent !important;
}

h1,
h2,
h3 {
  font-family: "Roustel", cursive;
}

h1 {
  font-size: 46px;
  line-height: 0.8;
}
@media (max-width: 767px) {
  h1 {
    font-size: 42px;
  }
}
h1 span {
  font-size: 26px;
  font-weight: 600;
  display: block;
  transform: translateY(1.5rem);
}
@media (max-width: 767px) {
  h1 span {
    font-size: 20px;
  }
}

h3 {
  font-size: 36px;
}
@media (max-width: 767px) {
  h3 {
    font-size: 28px;
  }
}

small {
  font-size: 12px;
}

span {
  font-size: 16px;
}

@media (max-width: 575px) {
  p,
  span {
    font-size: 14px;
  }
  small {
    font-size: 10px;
  }
}
.img-fluid {
  position: relative;
  width: 100%;
  height: auto;
}

@media (max-width: 767px) {
  .container {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

.btn-custom {
  background-color: var(--color-secondary);
  padding: 0.5rem 2rem;
  transition: all 0.3s ease-in-out;
  color: var(--text-button);
  width: fit-content;
  margin: 0 auto;
  border-radius: 5px;
  box-shadow: 2px 5px 1rem rgba(0, 0, 0, 0.1);
}
.btn-custom:hover {
  color: var(--text-button);
}
.btn-custom path {
  fill: var(--text-button);
}

.border-custom {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--border-radius);
  border: 2px solid var(--color-secondary);
}

.overlay-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 1000px 1000px 50px 50px;
  background-color: var(--color-secondary);
}

.font-primary {
  font-family: "Open Sans", sans-serif;
}

.font-secondary {
  font-family: "Roustel", cursive;
}

.scaleX-reverse {
  transform: scaleX(-1);
}

.scaleY-reverse {
  transform: scaleY(-1);
}

.scale-reverse {
  transform: scale(-1);
}

.form-control,
.form-select {
  font-family: "Open Sans", sans-serif;
  border: 1px solid var(--color-dark);
  padding: 0.5rem 1rem;
  background-color: #fff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  border-radius: 4px;
}
.form-control::placeholder,
.form-select::placeholder {
  color: var(--color-dark);
}
.form-control:focus,
.form-select:focus {
  box-shadow: none;
  border-color: var(--color-dark);
}

.scrolled {
  background: #fff !important;
  transition: all 1s ease-in-out;
  box-shadow: 3px 0px 15px 15px rgba(89, 89, 89, 0.2);
  visibility: visible !important;
  pointer-events: all !important;
}
.scrolled .logo-viding path {
  fill: var(--vd-color-pink);
}

.navbar-hide {
  top: -70px;
}

.progress {
  position: fixed;
  top: 0;
  left: 0;
  height: 2px;
  background-color: var(--vd-color-pink);
  width: 0;
  z-index: 9999;
}

.title-section {
  position: relative;
  width: fit-content;
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 1.25rem;
}
.title-section h2,
.title-section p,
.title-section span {
  margin-bottom: 0;
  font-weight: 500;
  font-size: 38px;
}
@media (max-width: 767px) {
  .title-section h2,
  .title-section p,
  .title-section span {
    font-size: 2rem;
  }
}

.overlay-elements-gradient {
  position: absolute;
  left: 0;
  width: 100%;
  height: 30%;
}
.overlay-elements-gradient.overlay-bottom {
  bottom: 0;
  background: linear-gradient(to top, var(--color-main) 20%, transparent 100%);
}
.overlay-elements-gradient.overlay-top {
  top: 0;
  background: linear-gradient(to bottom, var(--color-main) 20%, transparent 100%);
}

.waves-bottom {
  position: absolute;
  width: 100%;
  left: 0;
  height: auto;
  bottom: -2px;
  z-index: 3;
}
.waves-bottom path {
  fill: var(--color-main);
}
.waves-bottom.reverse {
  transform: scaleX(-1);
}

.waves-top {
  position: absolute;
  top: -2px;
  left: 0;
  width: 100%;
  height: auto;
  transform: scale(-1);
  z-index: 3;
}
.waves-top path {
  fill: var(--color-main);
}

.waves-left {
  position: absolute;
  height: 100%;
  left: -1px;
  top: 0;
}

.waves-right {
  position: absolute;
  height: 100%;
  right: -1px;
  top: 0;
  transform: scale(-1);
}

ul {
  padding: 0;
}
ul li {
  padding: 0;
}

@media (min-width: 768px) {
  .lg {
    display: block;
  }
  .sm {
    display: none;
  }
}
@media (max-width: 767px) {
  .lg {
    display: none;
  }
  .sm {
    display: block;
  }
}
.loader-wrapper {
  position: fixed;
  min-height: 100vh;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  z-index: 1500;
  flex-direction: column;
  gap: 1.25rem;
}
.loader-wrapper .spinner {
  position: relative;
  width: 84px;
}
.loader-wrapper span {
  color: var(--vd-color-pink);
  font-weight: 700;
  animation: fade 2s linear infinite;
  letter-spacing: 5px;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.modal.show-maps .modal-content {
  border-radius: 20px;
}
.modal.show-maps .modal-body {
  padding: 2rem 3rem;
  position: relative;
}
@media (max-width: 767px) {
  .modal.show-maps .modal-body {
    padding: 2rem 1rem;
  }
}
.modal.show-maps .modal-body .btn-modal {
  background-color: var(--color-main);
  color: var(--color-dark);
  position: absolute;
  right: 10px;
  top: 10px;
  border-radius: 50px;
  opacity: 0.5;
  padding: 0 0.425rem;
  transition: all 0.5s ease;
}
.modal.show-maps .modal-body .btn-modal:hover {
  opacity: 1;
}
.modal.show-maps .modal-body .btn-custom {
  background: transparent;
  border-color: var(--color-dark);
  color: var(--color-dark);
  transition: all 0.25 ease-in-out;
}
.modal.show-maps .modal-body .btn-custom:hover {
  background: var(--color-secondary);
  border-color: var(--color-secondary);
  color: var(--text-button);
}
.modal.show-maps .modal-body .maps-element {
  position: relative;
  width: 100%;
  aspect-ratio: 16/9;
}
@media (max-width: 767px) {
  .modal.show-maps .modal-body .maps-element {
    aspect-ratio: 9/10;
  }
}
.modal.show-maps .modal-body .loader-wrapper-modal {
  display: block;
  width: 100%;
  height: 100%;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal.show-maps .modal-body .loader-wrapper-modal.loaded {
  display: none;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div {
  position: absolute;
  width: 6px;
  height: 6px;
  background: var(--color-secondary);
  border-radius: 50%;
  animation: lds-default 1.2s linear infinite;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(1) {
  animation-delay: 0s;
  top: 37px;
  left: 66px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(2) {
  animation-delay: -0.1s;
  top: 22px;
  left: 62px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(3) {
  animation-delay: -0.2s;
  top: 11px;
  left: 52px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(4) {
  animation-delay: -0.3s;
  top: 7px;
  left: 37px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(5) {
  animation-delay: -0.4s;
  top: 11px;
  left: 22px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(6) {
  animation-delay: -0.5s;
  top: 22px;
  left: 11px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(7) {
  animation-delay: -0.6s;
  top: 37px;
  left: 7px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(8) {
  animation-delay: -0.7s;
  top: 52px;
  left: 11px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(9) {
  animation-delay: -0.8s;
  top: 62px;
  left: 22px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(10) {
  animation-delay: -0.9s;
  top: 66px;
  left: 37px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(11) {
  animation-delay: -1s;
  top: 62px;
  left: 52px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(12) {
  animation-delay: -1.1s;
  top: 52px;
  left: 62px;
}
@keyframes lds-default {
  0%, 20%, 80%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
}
.modal.show-maps .modal-body .modal-actions {
  position: relative;
  color: var(--color-dark);
}

/** mouse scroll **/
.mouse_scroll {
  display: block;
  margin: 0;
  width: 10px;
  position: relative;
}

.m_scroll_arrows {
  display: block;
  width: 5px;
  height: 5px;
  -ms-transform: rotate(45deg);
  /* IE 9 */
  -webkit-transform: rotate(45deg);
  /* Chrome, Safari, Opera */
  transform: rotate(45deg);
  border-right: 2px solid var(--color-dark);
  border-bottom: 2px solid var(--color-dark);
  margin: 0 0 3px 4px;
  width: 14px;
  height: 14px;
}
@media (max-width: 767px) {
  .m_scroll_arrows {
    width: 10px;
    height: 10px;
  }
}

.unu {
  margin-top: 1px;
}

.unu,
.doi,
.trei {
  -webkit-animation: mouse-scroll 1s infinite;
  -moz-animation: mouse-scroll 1s infinite;
  animation: mouse-scroll 1s infinite;
}

.unu {
  -webkit-animation-delay: 0.1s;
  -moz-animation-delay: 0.1s;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
  animation-delay: alternate;
}

.doi {
  -webkit-animation-delay: 0.2s;
  -moz-animation-delay: 0.2s;
  -webkit-animation-direction: alternate;
  animation-delay: 0.2s;
  animation-direction: alternate;
  margin-top: -6px;
}

.trei {
  -webkit-animation-delay: 0.3s;
  -moz-animation-delay: 0.3s;
  -webkit-animation-direction: alternate;
  animation-delay: 0.3s;
  animation-direction: alternate;
  margin-top: -6px;
}

.mouse {
  height: 32px;
  width: 18px;
  border-radius: 14px;
  transform: none;
  border: 2px solid var(--color-dark);
  top: 170px;
}

.wheel {
  height: 5px;
  width: 2px;
  display: block;
  margin: 5px auto;
  background: var(--color-dark);
  position: relative;
  height: 4px;
  width: 4px;
  border: 2px solid var(--color-dark);
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.wheel {
  -webkit-animation: mouse-wheel 0.6s linear infinite;
  -moz-animation: mouse-wheel 0.6s linear infinite;
  animation: mouse-wheel 0.6s linear infinite;
}

@keyframes mouse-wheel {
  0% {
    top: 1px;
  }
  25% {
    top: 2px;
  }
  50% {
    top: 3px;
  }
  75% {
    top: 2px;
  }
  100% {
    top: 1px;
  }
}
@keyframes mouse-scroll {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
[data-anim][data-anim] {
  will-change: transform;
}
[data-anim][data-anim].animation-invisible {
  visibility: hidden;
}
[data-anim][data-anim].has-animate {
  animation-duration: 2500ms;
  animation-timing-function: ease-in-out;
}
[data-anim][data-anim].animate-loop {
  animation-duration: 4000ms;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
[data-anim][data-anim].animate-loop.animate-paused {
  animation-play-state: paused;
}

[data-anim=fade].has-animate {
  animation-name: fadeAnim;
}

[data-anim=fade-right].has-animate {
  animation-name: fadeRight;
}

[data-anim=fade-left].has-animate {
  animation-name: fadeLeft;
}

[data-anim=fade-up].has-animate {
  animation-name: fadeUp;
}

[data-anim=fade-down].has-animate {
  animation-name: fadeDown;
}

@keyframes fadeAnim {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeRight {
  0% {
    opacity: 0;
    transform: translate3d(-100px, 0, 0);
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
@keyframes fadeLeft {
  0% {
    opacity: 0;
    transform: translate3d(100px, 0, 0);
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100px, 0);
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
@keyframes fadeDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100px, 0);
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
[data-anim=slide-right].has-animate {
  animation-name: slideRight;
}

[data-anim=slide-left].has-animate {
  animation-name: slideLeft;
}

[data-anim=slide-up].has-animate {
  animation-name: slideUp;
}

[data-anim=slide-down].has-animate {
  animation-name: slideDown;
}

@keyframes slideRight {
  0% {
    transform: translate3d(-30%, 0, 0);
  }
  100% {
    transform: translateZ(0);
  }
}
@keyframes slideLeft {
  0% {
    transform: translate3d(30%, 0, 0);
  }
  100% {
    transform: translateZ(0);
  }
}
@keyframes slideUp {
  0% {
    transform: translate3d(0, 50%, 0);
  }
  100% {
    transform: translateZ(0);
  }
}
@keyframes slideDown {
  0% {
    transform: translate3d(0, -100px, 0);
  }
  100% {
    transform: translateZ(0);
  }
}
[data-anim=slide-rotate-left].has-animate {
  animation-name: slideRotateLeft;
}

[data-anim=slide-rotate-right].has-animate {
  animation-name: slideRotateRight;
}

[data-anim=slide-rotate-up].has-animate {
  animation-name: slideUpRotateLeft;
}

@keyframes slideRotateLeft {
  0% {
    opacity: 0;
    transform: translate3d(30%, 0, 0) rotate(45deg);
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) rotate(0);
  }
}
@keyframes slideRotateRight {
  0% {
    opacity: 0;
    transform: translate3d(-30%, 0, 0) rotate(-45deg);
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) rotate(0);
  }
}
@keyframes slideUpRotateLeft {
  0% {
    opacity: 0;
    transform: translate3d(0, 150px, 0) rotate(30deg);
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) rotateZ(0);
  }
}
[data-anim=rotate-right].has-animate {
  animation-name: rotateRight;
}

[data-anim=rotate-left].has-animate {
  animation-name: rotateLeft;
}

@keyframes rotateRight {
  0% {
    transform: rotateZ(0);
  }
  100% {
    transform: rotate(-15deg);
  }
}
@keyframes rotateLeft {
  0% {
    transform: rotateZ(0);
  }
  100% {
    transform: rotate(15deg);
  }
}
[data-anim=zoom-in].has-animate {
  animation-name: zoomIn;
}

[data-anim=zoom-in-up].has-animate {
  animation-name: zoomInUp;
}

[data-anim=zoom-in-down].has-animate {
  animation-name: zoomInDown;
}

[data-anim=zoom-in-left].has-animate {
  animation-name: zoomInLeft;
}

[data-anim=zoom-in-right].has-animate {
  animation-name: zoomInRight;
}

[data-anim=zoom-out].has-animate {
  animation-name: zoomOut;
}

[data-anim=zoom-out-up].has-animate {
  animation-name: zoomOutUp;
}

[data-anim=zoom-out-down].has-animate {
  animation-name: zoomOutDown;
}

@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
@keyframes zoomInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100px, 0) scale3d(0.3, 0.3, 0.3);
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
@keyframes zoomInDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100px, 0) scale3d(0.3, 0.3, 0.3);
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
@keyframes zoomInLeft {
  0% {
    opacity: 0;
    transform: translate3d(50%, 0, 0) scale3d(0.3, 0.3, 0.3);
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
@keyframes zoomInRight {
  0% {
    opacity: 0;
    transform: translate3d(-50%, 0, 0) scale3d(0.3, 0.3, 0.3);
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
@keyframes zoomOut {
  0% {
    opacity: 0;
    transform: scale3d(1.3, 1.3, 1.3);
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
@keyframes zoomOutUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100px, 0) scale3d(1.3, 1.3, 1.3);
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
@keyframes zoomOutDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100px, 0) scale3d(1.3, 1.3, 1.3);
