.venue-section {
	position: relative;
	background: var(--color-primary);
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			padding: 5rem 0;
			@include max-md {
				padding: 3rem 0;
			}
			.ribbon-venue {
				position: relative;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
			}
			.card {
				background-color: transparent;
				height: 100%;
				border-radius: 5px;
				z-index: 5;
				text-align: left;
				overflow: hidden;
				border: none;
				@include max-md {
					text-align: center;
				}
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: var(--color-main);
					opacity: 0.8;
					content: "";
					border-radius: 5px;
				}
				.card-body {
					position: relative;
					padding: 3rem;
					@include max-lg {
						padding: 2rem;
					}
					h4 {
						margin: 0 0 1rem;
					}
					.title-event {
						font-family: $font-secondary;
						font-size: 32px;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.event-name {
		position: relative;
		width: 100%;
		.date {
			position: relative;
			width: 100%;
			display: flex;
			flex-direction: column;
			margin: 0.5rem 0;
			span {
				font-weight: 600;
				margin-bottom: 0;
				font-size: 22px;
			}
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
		h4 {
			font-family: $font-primary !important;
		}
		.btn-custom {
			background-color: transparent;
			border: 1px solid var(--color-dark);
			color: var(--color-button-trans);
			&:hover {
				background-color: var(--color-secondary) !important;
				border-color: var(--color-secondary);
				color: var(--text-button);
			}
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
