.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin: 1rem 0;
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			width: 35%;
			height: 100%;
			margin: 0 0 0 auto;
			padding: 4.5rem 0 4rem;
			@include max-xl {
				width: 40%;
			}
			@include max-lg {
				width: 60%;
				display: flex;
				align-items: flex-end;
				justify-content: flex-end;
			}
			@include max-md {
				width: 90%;
			}
			.header-title {
				position: relative;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				text-align: left;
				padding: 3rem;
				width: 100%;
				height: 100%;
				@include max-lg {
					height: auto;
					min-height: 50%;
				}
				@include max-md {
					padding: 2rem;
				}
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background: var(--color-main);
					opacity: 0.8;
					content: "";
				}
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 80%;
				height: auto;
				aspect-ratio: 4/6;
				margin: 0 auto;
				align-self: flex-end;
				.foreground-element {
					width: 100%;
					height: 100%;
					mask-image: url(../images/mask-photo.png);
					mask-size: 100% 100%;
					mask-position: top;
					mask-repeat: no-repeat;
					&::before {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						background-color: rgba($color: #fff, $alpha: 0.5);
						filter: blur(5px);
						content: "";
					}
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
				.image-element {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 90%;
					height: 93%;
					mask-image: url(../images/mask-photo.png);
					mask-size: 100% 100%;
					mask-position: top;
					mask-repeat: no-repeat;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
