.couple-section {
	position: relative;
	@include max-md {
		text-align: center;
	}
	.and {
		position: absolute;
		top: 100%;
		right: 0;
		transform: translate(30%, -50%);
		padding: 0.75rem 1.47rem;
		font-weight: 600;
		width: fit-content;
		z-index: 5;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-main);
			opacity: 0.8;
			border-radius: 50px;
			content: "";
		}
		span {
			position: relative;
			font-size: 32px;
			font-family: $font-secondary;
			margin-right: 0.2rem;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0 0;
		@include max-md {
			padding: 3rem 0 0;
		}
		.title-section {
			margin: 0 0 1.25rem;
			@include max-md {
				margin: 0 auto 1.25rem;
			}
		}
	}
	.couple-body {
		position: relative;
		width: 100%;
		background-color: var(--color-primary);
		.text-vertical {
			position: absolute;
			font-size: calc(12vw + 0.5rem);
			font-family: $font-secondary;
			writing-mode: vertical-lr;
			top: 50%;
			transform: translate(0, -50%);
			opacity: 0.15;
			@include max-md {
				font-size: calc(23vw + 0.5rem);
			}
			&.man {
				right: 0;
				@include max-md {
					right: -2rem;
				}
			}
			&.woman {
				left: 0;
				@include max-md {
					left: -1rem;
				}
			}
		}
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		width: 70%;
		text-align: center;
		@include max-md {
			width: 75%;
		}
		&.man {
			margin: 0 auto 0 0;
		}
		&.woman {
			margin: 0 0 0 auto;
		}
		.image-wrap {
			width: 100%;
			aspect-ratio: 4/3;
			position: relative;
			@include max-lg {
				aspect-ratio: 3/4;
			}
			@include max-md {
				aspect-ratio: 4/6;
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				overflow: hidden;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
				}
			}
		}
		.couple-description {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			padding: 2rem 2rem 2rem 5rem;
			text-align: left;
			@include max-md {
				padding: 2rem;
			}
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-main);
				opacity: 0.8;
				content: "";
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			margin: 0 auto 0 0;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
