.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: left;
	@include max-md {
		padding: 3rem 0;
	}
	.thank-wrapper {
		position: relative;
		min-height: 70vh;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		@include max-lg {
			align-items: flex-end;
			min-height: 50vh;
		}
		.thank {
			position: relative;
			width: 40%;
			padding: 3rem;
			@include max-lg {
				width: 50%;
			}
			@include max-md {
				width: 90%;
			}
			&::before {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				background-color: var(--color-light);
				opacity: 0.8;
				content: "";
			}
			@include max-md {
				padding: 2rem;
			}
			.title-section {
				margin: 0 0 1.25rem;
			}
			p {
				margin-bottom: 0;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
