.story-section {
	position: relative;
	background: var(--color-primary);
	.story {
		position: relative;
		padding: 0 0 5rem;
		@include max-md {
			padding: 0 0 3rem;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		padding: 3rem 2rem;
		text-align: center;
		margin-top: -8rem;
		@include max-md {
			margin-top: -5rem;
		}
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: var(--color-main);
			opacity: 0.8;
			content: "";
		}
		@include max-md {
			padding: 2rem 1rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
