.wishes-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.card-form {
		margin-bottom: 0;
		background: transparent;
		position: relative;
		z-index: 3;
		&.card {
			border: none;
		}
		.card-body {
			padding: 0 2rem;
			@include max-md {
				padding: 0 1rem;
			}
			.form-control {
				background: transparent;
			}
		}
	}

	.wishes-preview {
		position: relative;
		&::before {
			position: absolute;
			top: 0;
			left: -1rem;
			width: 250%;
			height: 100%;
			background-color: var(--color-primary);
			content: "";
		}
		@include max-lg {
			margin-top: 2rem;
		}
		.text-trans-custom {
			position: absolute;
			top: 50%;
			left: 0;
			transform: translate(10%, -50%);
			font-family: $font-secondary;
			font-size: calc(12vw + 0.5rem);
			opacity: 0.1;
			@include max-md {
				font-size: calc(30vw + 0.5rem);
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			border-radius: var(--border-radius);
			.card-body {
				overflow: hidden;
				padding: 2rem 0;
				.wishes {
					text-align: left;
					height: 28.5rem;
					overflow: auto;
					padding: 1rem;
					color: var(--color-dark);
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 1);
							border-radius: 5px;
							position: relative;
							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 1);
							}
						}
					}
				}
			}
		}
	}
}
