.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 0 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 0 0 8rem;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			width: 100%;
			height: 100%;
			display: flex;
			align-items: flex-end;
			justify-content: center;
			padding: 4.5rem 1rem 2rem;
			overflow: hidden;
			@include min-md {
				width: 65%;
				min-height: 80%;
				height: auto;
			}
			@include min-lg {
				width: 50%;
			}
			@include min-xl {
				width: 40%;
			}
			&::before {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: rgba($color: #fff, $alpha: 0.6);
				content: "";
			}
			.cover-body {
				position: relative;
				width: 100%;
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.cover-description {
					position: relative;
					padding: 0 2rem;
					text-align: left;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					.title {
						color: var(--color-dark);
						margin: 1.25rem 0;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
